  width: 102rem;
  height: 102rem;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 50%;
  top: 37%;
  transform: translate(-50%, -50%);
}
.harmony-bg__outer {
  border: 2px dashed rgba(17, 34, 25, 0.1);
  width: 102rem;
  height: 102rem;
  border-radius: 50%;
  animation: rotate 15s linear infinite;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.harmony-bg__outer span {
  background-color: #fc5b31;
  position: absolute;
  border-radius: 50%;
  width: 1.2rem;
  height: 1.2rem;
  transform: translate(-50%, -50%);
}
.harmony-bg__outer span:nth-child(1) {
  top: 1.5rem;
  right: 37.5rem;
}
.harmony-bg__outer span:nth-child(2) {
  right: 1rem;
  top: 35.5rem;
  outline: 1px solid #fc5b31;
  outline-offset: 0.8rem;
}
.harmony-bg__outer span:nth-child(3) {
  left: 2.3rem;
  top: 35.5rem;
}
.harmony-bg__middle {
  width: 57.3rem;
  height: 57.3rem;
  position: absolute;
  top: 22.3rem;
  right: 22.3rem;
  bottom: 22.3rem;
  left: 22.3rem;
  border-radius: 50%;
  border: 1px solid rgba(17, 34, 25, 0.1);
  animation: rotateMiddle 20s linear infinite;
}
.harmony-bg__middle span {
  background-color: #fc5b31;
  position: absolute;
  border-radius: 50%;
  width: 1.2rem;
  height: 1.2rem;
  transform: translate(-50%, -50%);
  outline: 1px solid #fc5b31;
  outline-offset: 0.8rem;
}
.harmony-bg__middle span:nth-child(1) {
  top: 2.5rem;
  right: 15.5rem;
}
.harmony-bg__middle span:nth-child(2) {
  top: 60%;
  left: 0.5rem;
}
.harmony-bg__text-box {
  width: 16rem;
  height: 16rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fc5b31;
}
.harmony-bg__text {
  color: #fdfdfd;
}

@media (max-width: 48em) {
