    width: 67rem !important;
  }
  .harmony-bottom__swiper-slide.swiper-slide:nth-child(6n+1) {
    transform: rotate(0) translateY(0);
  }
  .harmony-bottom__swiper-slide.swiper-slide:nth-child(6n+2) {
    transform: rotate(0);
  }
  .harmony-bottom__swiper-slide.swiper-slide:nth-child(6n+3) {
    transform: rotate(0) translate(0);
  }
  .harmony-bottom__swiper-slide.swiper-slide:nth-child(6n+4) {
    transform: rotate(0) translateY(0);
  }
  .harmony-bottom__swiper-slide.swiper-slide:nth-child(6n+5) {
    transform: rotate(0);
  }
  .harmony-bottom__swiper-slide.swiper-slide:nth-child(6n+6) {
